
html,
body {
  height: 100%;
  overflow: hidden;
}
body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.container {
  min-height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.main-content {
  /*padding: 16px;*/
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  overflow: auto;
  -webkit-transform: translate3D(0, 0, 0);
          transform: translate3D(0, 0, 0);
  background-color: #fafafa;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
